<template>
<div class="row">
    <!--right-->
    <div data-app class="col-xs-12 col-md-6" v-if="ready_member">
        
        <label>Member Management</label>
        <div style="display:flex;flex-direction:row">
            <div class="form-group">
                <input type="text" class="form-control" v-model="search_user_key" @keyup="search_user" placeholder="Search User"/>
            </div>
            <div class="form-group">
                <select class='form-control select' v-model="title" style="margin-left:1em">
                     <option v-for="t in title_list ">{{ t }}</option>
            </div>
        </div>
            <v-data-table
            :headers="headers"
            :items="userdata"
            :search="search"
            class="elevation-1"
            :rows-per-page-items="10"
            v-if="hv_user"
            >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.id v-on:click.prevent="add_user($event)">Add</a></td>
                <td>{{ props.item.name }}</td>
                <td>{{ props.item.username }}</td>   
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>
            <v-data-table
            :headers="headers_list"
            :items="response_member"
            :search="search"
            class="elevation-1"
            :rows-per-page-items="10"
            >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.id v-on:click.prevent="remove_user($event)">Remove</a></td>
                <td>{{ props.item.name }}</td>
                <td>{{ props.item.username }}</td> 
                <td style='width:5em;text-align: center; '>{{ props.item.counter }}</td>   
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>        
            </template>
            </v-data-table><br/>
            <!--member-->
            <!--table_unhandle-->
    <label>Un-Handled Tasks</label>
    <div data-app class="table-responsive">
        <v-data-table
            :headers="headers_pool"
            :items="pooldata"
            class="elevation-1"
            :rows-per-page-items="10"
        >
            <template v-slot:items="props">
                <td style='width:5em'>{{ props.item.item_id }}</td>
                <td>{{ props.item.Req_Name }}</td>
                <td style='width:55em'>{{ props.item.Description }}</td>
                <td>{{ props.item.Location }}</td>
                <td>{{ props.item.Request_for }}</td>
            
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>    
            </template>
        </v-data-table>
    </div>
    </div> <!--incomplete-->
<!--left-->
        <div class="col-md-6" style="margin-top:4.85em"><!--chart-->
            <card header-text="Request Chart">
            <div class="chart-wrapper">
                <line-chart-js/>
            </div>
            </card>
        </div><!--chart-->
    </div><!--left-->
</template>

<script>
import Info from '../views/dashboard/InfoBox.vue';
import InfoChart from '../views/dashboard/InfoBoxChart.vue';
import InfoChartTwo from '../views/dashboard/InfoBoxChart2.vue';
import LineChartJs from '../components/charts/chartjs/scripts/LineChartJs.vue';

export default {
    components: {
        Info,
        InfoChart,
        InfoChartTwo,
        LineChartJs,
    },
    data(){
        return{
            response_member:[],  
            userdata:[],
            pooldata:[],
            headers: [
          { text: 'Action',value :'id',sortable: false},
          { text: 'Name',value:'name',sortable: false},
          { text: 'UserID',value:'username',sortable: false},
        ], 
            headers_list: [
          { text: 'Action',value :'id',sortable: false},
          { text: 'Name',value:'name',sortable: false},
          { text: 'UserID',value:'username',sortable: false},
          { text: 'Handles',value:'counter',sortable: false},
        ], 
         headers_pool: [
          {text: 'ID',value :'id',sortable: false},
          { text: 'Requester',value:'req',sortable: false},
          { text: 'Description',value:'Descript',sortable: false},
          { text: 'Location',value:'loc',sortable: false},
          { text: 'Request For',value:'rf',sortable: false}
        ], 
        title_list:['IR Member','ITSD & Resources','Academic','Admin'],
        title:'IR Member',
        ready_member:false,
        search_user_key:"",
        hv_user:false  ,
        loaded_complete:false ,
        loaded:false,
        total:0,
        req:[],
        top_req:"",
        count_top_req:0,
        }
    },
    async created(){
        await axios.get('RequestPortal/backend/ir_member.php')
            .then(
                (response)=>{
                    this.response_member = response.data;
                    this.response_member.id = response.data.id;
                    this.response_member.username = response.data.username;
                    this.response_member.name = response.data.name;
                    this.response_member.counter = response.data.counter;
                },
                this.ready_member = true
            ),
        axios.get('RequestPortal/backend/unhandle.php')
        .then(
            response=>{
                this.pooldata = response.data
            }
        );
        await axios.get('RequestPortal/backend/top_requester.php')
        .then(
            (response)=>{
                this.req = response.data;
                this.top_req = "Top Requester : "+this.req[0].Requester;
                this.count_top_req = this.req[0].counter;              
                this.loaded_complete = true;
            }
        )
        await axios.get('RequestPortal/backend/fetchtotal.php')
           .then((response) => {
            this.total = response.data.total;
            this.loaded = true;
        });
    },
    methods:{
        remove_user(event){
            var targetID = event.currentTarget.id;
            var formData = new FormData();
            formData.append('id', targetID);
            axios.post('RequestPortal/backend/new_member_list.php',formData)
            .then(
                (response)=>{
                    this.response_member = response.data;
                    this.response_member.id = response.data.id;
                    this.response_member.username = response.data.username;
                    this.response_member.name = response.data.name;
                });
        },
        search_user(){
            var formData = new FormData();
            formData.append('key', this.search_user_key);
            axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_user=false; 
                    } else {
                        this.userdata = response.data;
                        this.userdata.id = response.data.id;
                        this.userdata.username = response.data.username;
                        this.userdata.name = response.data.name;
                        this.hv_user=true;
                    }
                }); 
        },
        add_user(event){
            var targetID = event.currentTarget.id;
            var formData = new FormData();
            formData.append('title',this.title);
            formData.append('id', targetID);
            axios.post('RequestPortal/backend/add_member.php',formData)
            .then(
                (response)=>{
                    this.response_member = response.data;
                    this.response_member.id = response.data.id;
                    this.response_member.username = response.data.username;
                    this.response_member.name = response.data.name;
                    this.hv_user=false;
                    this.search_user_key="";
                }); 
        }
    }


}
</script>



